export async function* streamiterator(stream) {
  const resolved = await stream;
  const reader = getStream(resolved) ?? getStream(resolved?.body);

  if (!reader) {
    throw new TypeError("argument is not a ReadableStream");
  } else if (isIterable(reader)) {
    // do not use async iteration over sync iterator
    // https://github.com/tc39/ecma262/issues/1849
    for (const chunk of reader) yield chunk;
  } else if (isAsyncIterable(reader)) {
    for await (const chunk of reader) yield chunk;
  } else if (isReader(reader)) {
    let needClose;

    try {
      for (;;) {
        needClose = true;
        const x = await reader.read();
        if (x.done) break;
        needClose = false;
        yield x.value;
      }
    } finally {
      if (!needClose) await reader.cancel?.();
    }
  } else {
    throw new TypeError("argument is not a ReadableStream");
  }
}

function getStream(value) {
  if (!value) {
    return undefined;
  }

  if (isReadable(value)) {
    return value;
  }

  if (isReadableStream(value)) {
    return value.getReader();
  }

  if (isBlob(value)) {
    return value.stream()?.getReader?.();
  }

  return undefined;
}

function isIterable(value) {
  return typeof value[Symbol.iterator] === "function";
}

function isAsyncIterable(value) {
  return typeof value[Symbol.asyncIterator] === "function";
}

function isReadableStream(value) {
  return typeof value.getReader === "function";
}

function isBlob(value) {
  return typeof value.stream === "function";
}

function isReader(value) {
  return typeof value.read === "function";
}

function isReadable(value) {
  return isIterable(value) || isAsyncIterable(value) || isReader(value);
}